import { Component, OnInit } from '@angular/core';
import { UiProductsServices } from '../uiProductsServices.service';
import { AdminServices } from '../admin/admin-service';
import { Subscription } from 'rxjs';
declare var $: any;

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent implements OnInit {
  products: any;
  productsCount;
  isLoading: Boolean = false;
  postsPerPage =12;
  pageSizeOptions = [12,6,3,18,21];
  currentPage = 1;
  notfound: Boolean = false;
  searchQuery = new Subscription;
  averageRate;
  cart: any;
  anim: string = '';
  value: number = 100;
  maxPrice = 399;
  productModal: Object = {};
  cartQuantity: number = 0;
  topLikedItems: any;
  order: string = 'date';
  catsNames=['brand','design','type','color','size'];
  cats = [];
  
  constructor(public uiProductsServices: UiProductsServices, public productsServices: AdminServices) { }

  ngOnInit() {
    this.searchQuery = this.uiProductsServices.searchQueryListener().subscribe(res=> {
      this.isLoading = true;
      this.productsServices.searchquery(res.searchquery).subscribe(res => { 
        console.log(res.items);
        if(res.items.length === 0){this.notfound = true;} else{this.notfound = false;}
        this.products = res.items;
        this.isLoading = false;
        }, err => {
          console.log(err);
          this.products = null;
          this.notfound = true;
          this.isLoading = false;
        });
    });
    this.isLoading = true;
      this.getAllItems();
      this.uiProductsServices.cat('brand').subscribe(res => {
        this.cats[0]=res.cats;
      });
       this.uiProductsServices.cat('design').subscribe(res => {
        this.cats[1]=res.cats;
      });
      this.uiProductsServices.cat('type').subscribe(res => {
        this.cats[2]=res.cats;
      });
      this.uiProductsServices.cat('color').subscribe(res => {
        this.cats[3]=res.cats;
      });
      this.uiProductsServices.cat('size').subscribe(res => {
        this.cats[4]=res.cats;
      });
        this.uiProductsServices.getTopLiked().subscribe(res=>{
          this.topLikedItems = res.topLiked;
          console.log(this.topLikedItems);
        });
      //////////////////////////////////Slider////////////////////////////////////////////////////////////////
  }
  
  private getAllItems() {
      this.uiProductsServices.getProducts(this.currentPage, this.postsPerPage, this.order).subscribe(res => {
        if(res.items.length ===0){this.notfound=true;}else{this.notfound=false;}
        this.products = res.items;
        this.productsCount = res.count;
        this.isLoading = false;
        if(res.cart){
          this.cart = res.cart;
          const qnt = res.cart.length;
          this. cartQuantity = res.cart.length;
          this.uiProductsServices.updatedCartQuantity.next({cartQuantity:qnt});
        }
        });
  }
  
  filter(f) {
    let filterObject = {};
    if(f.value.newPrice>0){ filterObject = {newPrice: {$lt: f.value.newPrice+1}}};
    let filterarray = [];
    const filtervalues = Object.keys(f.value);
    for(let filtervalue of filtervalues) {
    if(f.value[filtervalue] === true) {
      let farr = filtervalue.split('/');
      filterarray.push(farr[0])
      filterObject[farr[1]] = filterarray;
      this.isLoading = true;
       }
      }
      this.uiProductsServices.filterize(filterObject).subscribe(res => {
        this.products = res.filteredItems;
        this.productsCount = res.count;
        this.isLoading = false;
        });
  }
  commingPage(comming) {
    let nops = Math.ceil(this.productsCount/this.postsPerPage);
    if(nops === this.currentPage && comming==="next") {return;}
    if(this.currentPage === 1 && comming === "previous") {return;}
    if(comming === 'next') {this.currentPage = this.currentPage + 1;}
    else if(comming === 'previous') {this.currentPage = this.currentPage - 1;}
    this.isLoading = true;
    this.getAllItems();
  }
  itemsPerPage(number) {
    this.postsPerPage = number;
    this.currentPage = 1;
    this.getAllItems();
  }
  addtocart(product) {
    this.uiProductsServices.pushToCart(product).subscribe(res=>{
      this.uiProductsServices.updatedCartQuantity.next({cartQuantity: res.count});
      this. cartQuantity = res.count;
      this.cart = res.updatedCart;
      if(res.cartcount == 0 ) {
      this.anim = 'bird';
      setTimeout(()=>{this.anim = ''},1000);
      }
    });
  }
  inCart(id) {
    let exp = ['fa fa-heart-o'];
    if(this.cart){
    for(let cart of this.cart) {
      if(cart.proid === id) {
        exp = ['fa fa-heart'];
        }
      }
    }
     return exp;
    }
    isNew(date) {
      if(date) {
        if((new Date().getTime() - new Date(date).getTime())<(14*24*60*60000)){
          return 'new';
        }
      }
    }
    modal(product) {
      if(product) {
      this.productModal = product;
      }
    }
    orderBy(order) {
      this.order = order;
      this.isLoading = true;
      this.getAllItems();
    }

}
